.button {
    font-family: Montserrat,sans-serif;
    font-size: 14px;
    font-weight: 600;
    line-height: 20px;
    text-align: center;
    color: #FFFFFF;
    width: 129px;
    height: 30px;
    border: 1px solid #0066CC;
    border-radius: 3px;
}

.disabled {
    background-color: #a17b35;
}

.secondary {
    color: #0066CC;
    background-color: #F4F4F4;
}

.default {
    background-color: #0066CC
}

.red {
    background-color: #a13535;
}

